import React from 'react'
import { Link } from 'gatsby'
import { motion, AnimatePresence } from 'framer-motion'
//Styled components
import { Container, Flex, DefaultSizes } from '../../styles/globalStyles'
import {Nav, NavHeader, CloseNav, NavList, NavFooter, SocialMediaNav} from '../../styles/navigationStyles'

import fb from '../../assets/images/icons/facebook.svg'
import ig from '../../assets/images/icons/instagram.svg'
import mg from '../../assets/images/icons/messenger.svg'
// import behance from '../../assets/images/icons/behance.svg'
import dribbble from '../../assets/images/icons/dribbble.svg'

// Routes
const navRoutes = [
    { id: 0, title: 'Serviços.', path: '#services' },
    { id: 1, title: 'Portfolio.', path: '/portfolio' },
    { id: 2, title: 'Contactos.', path: '/404' },
    { id: 3, title: 'Some BS.', path: '/404' }
]

const Navigation = ({toggleMenu, setToggleMenu}) => {
    return(
        <>
            {/* Must define transition */}
            <AnimatePresence>
                {toggleMenu && (
                    <Nav initial={{y: '-100%'}} exit={{y: '-100%'}} animate={{y: toggleMenu ? 0 : '-100%'}} transition={{duration: .8, ease:[.6, .05, -.01, .9]}}>
                    <Container>
                        <NavHeader>
                            <Flex spaceBetween noHeight>
                                <h2>find your path.</h2>
                                <CloseNav onClick={() => setToggleMenu(!toggleMenu)}>
                                    <button>
                                        <span></span>
                                        <span></span>
                                    </button>
                                    
                                </CloseNav>
                            </Flex>
                        </NavHeader>
        
                        <NavList>
                            <DefaultSizes>
                            <ul>
        
                                {navRoutes.map(route => (
                                    <li key={route.id}>
                                        <Link to={`${route.path}`}  onClick={() => setToggleMenu(!toggleMenu)}>
                                            
                                            <motion.div
                                                initial={{x: -108}} whileHover={{x: -36.02, transition: { duration: .4, ease: [.6, .05, -.01, .9]}}}
                                            className="link">
                                                <span className="arrow">
                                                    
                                                    <svg version="1.1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 46.02 46.02" width="46.02px" height="46.02px"
                                                        >
                                                        <path d="M14.757,46.02c-1.412,0-2.825-0.521-3.929-1.569c-2.282-2.17-2.373-5.78-0.204-8.063l12.758-13.418L10.637,9.645
                                                                C8.46,7.37,8.54,3.76,10.816,1.582c2.277-2.178,5.886-2.097,8.063,0.179l16.505,17.253c2.104,2.2,2.108,5.665,0.013,7.872
                                                                L18.893,44.247C17.77,45.424,16.267,46.02,14.757,46.02z"/>

                                                    </svg>

                                                    
                                                </span>
                                                {route.title}
                                            </motion.div>
                                            
                                        </Link>
                                    </li>
                                ))}
        
                                
                            </ul>
                            </DefaultSizes>
                        </NavList>

                        <NavFooter>
                            <SocialMediaNav>
                                <Link to="https://www.facebook.com/pathfinderdesigns"><img src={fb} alt="FB"/></Link>
                                <Link to="https://www.instagram.com/pathfinderdesigns"><img src={ig} alt="IG"/></Link>
                                <Link to="https://www.m.me/pathfinderdesigns"><img src={mg} alt="MG"/></Link>
                                {/* <Link to="https://www.m.me/pathfinderdesigns"><img src={behance} alt="BH"/></Link> */}
                                <Link to="https://dribbble.com/rodrigorocha"><img src={dribbble} alt="DR"/></Link>
                            </SocialMediaNav>
                        </NavFooter>
                    </Container>
                </Nav>
                )}
            </AnimatePresence>
        </>
    )
}

export default Navigation