import styled from 'styled-components';
import {motion} from 'framer-motion'

export const FooterContainer = styled(motion.div)`
    padding: 70px 10vw;
    row-gap: 50px;
    display: flex;
    flex-direction: column;
    align-items: top;
    justify-content: space-between;

    @media screen and (min-width:700px) {
        flex-direction: row;
    }
`
export const FooterWrapper = styled(motion.div)`
    background: black;
    color:  ${props => props.theme.defaultWhite};
`

export const FooterSection = styled(motion.div)`
    * {
        list-style: none;
        text-decoration: none;
        color: ${props => props.theme.defaultWhite};
    }

    ul {
        
        line-height: 40px;
    }
`

export const FooterBrand = styled(motion.div)`

    img {
        width: 50px;
    }
    
`

export const SocialMedia = styled(motion.div)`
    display: flex;
    column-gap: 30px;
    row-gap: 30px;

    img {
        width: 20px;
        filter: ${props => props.theme.invert};
    }
    
`