import React, { useEffect } from 'react'
import { Link } from 'gatsby'
// import { Container } from '../styles/globalStyles'
import { FooterContainer, FooterBrand, SocialMedia, FooterSection, FooterWrapper } from '../styles/footerStyles'

import logo from '../assets/images/logo/logo.svg'
import fb from '../assets/images/icons/facebook.svg'
import ig from '../assets/images/icons/instagram.svg'
import mg from '../assets/images/icons/messenger.svg'
// import behance from '../assets/images/icons/behance.svg'
import dribbble from '../assets/images/icons/dribbble.svg'

//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'

const Footer = () => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        // rootMargin: '-250px',
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])

    return(
        <FooterWrapper>
            <FooterContainer 
                ref={contentRef} 
                animate={animation} 
                initial='hidden' 
                variants={
                    {hidden: {
                        opacity: 0,
                        y: 72
                    }, 
                    visible: {
                        opacity: 1,
                        y:0,
                        transition: {
                            duration: 0.6, 
                            ease: [0.6, 0.05, -0.01, 0.9]
                            }
                        }
                    }
                }>
                <FooterBrand>
                    <img src={logo} alt="adsdas" />
                </FooterBrand>
                <FooterSection>
                    <h2>Navegação</h2>
                    <ul>
                        <li><Link to="">Serviços</Link></li>
                        <li><Link to="">Portfolio</Link></li>
                        <li><Link to="">Contactos</Link></li>
                    </ul>
                </FooterSection>
                
                <FooterSection>
                    <SocialMedia>
                        <Link to="https://www.facebook.com/pathfinderdesigns"><img src={fb} alt="FB"/></Link>
                        <Link to="https://www.instagram.com/pathfinderdesigns"><img src={ig} alt="IG"/></Link>
                        <Link to="https://www.m.me/pathfinderdesigns"><img src={mg} alt="MG"/></Link>
                        {/* <Link to="https://www.m.me/pathfinderdesigns"><img src={behance} alt="BH"/></Link> */}
                        <Link to="https://dribbble.com/rodrigorocha"><img src={dribbble} alt="DR"/></Link>
                        
                    </SocialMedia>
                </FooterSection>
                
            </FooterContainer>
        </FooterWrapper>
        
        
    )
}

export default Footer