/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React, { useState } from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import { createGlobalStyle, ThemeProvider, normalize } from 'styled-components'

import Header from "./header"
import Footer from "./footer"
import Navigation from "./navigation/navigation"
import "./layout.css"

// Context
import {useGlobalStateContext} from '../context/globalContext'

const GlobalStyle = createGlobalStyle`
  
  * {
    text-decoration: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif !important;
    /* cursor: none */
  }

  html {
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    font-size: 16px;
    
  }

  body {
    
    background: ${props => props.theme.background};
    overscroll-behavior: none;
    overflow-x: hidden;
    
  }

`

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  const darkTheme = {
    defaultBlack: '#323232',
    defaultWhite: '#fff',
    background: '#fadadd',
    text: '#323232',
    // red: '#ea291e',
    red: '#ffc95e',
    filterRed: 'invert(84%) sepia(15%) saturate(1715%) hue-rotate(338deg) brightness(101%) contrast(101%)',
    invert: 'invert(100%)',
    filterInvert: 'invert(100%) sepia(0%) saturate(2%) hue-rotate(131deg) brightness(102%) contrast(101%);',
    // imageGradient: 'linear-gradient(rgba(0,0,0, 0) 0%, rgba(0,0,0, 0.5) 50%, rgba(0,0,0, 1) 100%)'
  }

  const lightTheme = {
    defaultBlack: '#323232',
    defaultWhite: '#fff',
    background: '#fff',
    text: '#323232',
    // red: '#ea291e',
    red: '#ffc95e',
    filterRed: 'invert(84%) sepia(15%) saturate(1715%) hue-rotate(338deg) brightness(101%) contrast(101%)',
    invert: 'invert(100%)',
    filterInvert: 'brightness(0) saturate(100%)',
    // imageGradient: 'linear-gradient(rgba(0,0,0, 0) 0%, rgba(255,255,255, 1) 100%)'
  }

  const currentTheme = useGlobalStateContext()

  // Toggle Navbar
  const [toggleMenu, setToggleMenu] = useState(false)

  return (
    <ThemeProvider theme={currentTheme === 'light' ? darkTheme: lightTheme}>
      <GlobalStyle />
      <Header 
        toggleMenu={toggleMenu}
        setToggleMenu={setToggleMenu}
      />
      <Navigation 
        toggleMenu={toggleMenu}
        setToggleMenu={setToggleMenu}
      />
        <main>{children}</main>
      <Footer />
    </ThemeProvider>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
