import styled from 'styled-components';
import {motion} from 'framer-motion'

export const Nav = styled(motion.div)`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    background: ${props => props.theme.red};
    color: #000;
    z-index: 100;
    overflow: hidden;
`

export const NavHeader = styled.div`
    top: 72px;
    position: relative;

    h2 {
        color: ${props => props.theme.text}
    }

    
`

export const CloseNav = styled.div`
    button {
        border: none;
        background: none;
        outline: none;
    }

    span {
        cursor: pointer;
        width: 36px;
        height: 4px;
        display: block;
        background: ${props => props.theme.text};
        /* margin: 8px; */

        &:first-child {
            transform: rotate(45deg);
            margin-bottom: 8px;
        }

        &:last-child {
            transform: rotate(-45deg) translate(8px, -8px);
            
        }
    }
`

export const NavList = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;

    ul {
        padding: 0;

        li {
            list-style: none;
            
            text-transform: lowercase;
            font-weight: 700;
            height: 96px;
            line-height: 96px;
            overflow: hidden;
            
            font-size: 1.9rem;

            .link {
                color: ${props => props.theme.text};
                position: relative;
                display: flex;
                align-items: center;
                .arrow {
                    height: 76px;
                    margin-right: 8px;

                    svg {
                        width: 100px;
                        path {
                            fill: ${props => props.theme.text};
                        }
                    }
                }

                
            }
        }

        @media screen and (min-width: 360px) {
            
            li {
                font-size: 2rem;
            }
        }
        @media screen and (min-width: 568px) {
            li {
                font-size: 2.1rem;
            }
        }

        @media screen and (min-width: 768px) {
            li {
                font-size: 2.2rem;
            }
        }
        @media screen and (min-width: 1244px) {
            li {
                font-size: 2.3rem;
            }
        }
    }
`

export const NavFooter = styled.div`
    position: absolute;
    bottom: 40px;
    
`

export const SocialMediaNav = styled.div`
    display: flex;
    flex-wrap: wrap;
    column-gap: 30px;

    img {
        width: 30px;
    }
`
