import styled, { css } from 'styled-components';
import { motion } from 'framer-motion'

export const Container = styled(motion.div)`
    flex-grow: 1;
    margin: 0 auto;
    padding: 0 10vw;
    position: relative;
    width: auto;
    height: 100%;
    /* @media (min-width: 1024px) {
        max-width: 960px;
    }
    @media (min-width: 1216px) {
        max-width: 1152px;
    }
    @media (min-width: 1408px) {
        max-width: 1244px;
    } */

    /* ${props => props.fluid && css`
        padding:0;
        margin:0;
        max-width: 100%;
    `} */
`

export const VerticalPadding = styled.div`
    padding: 30px 0;
`

export const DefaultSizes = styled.div`
    color: ${props => props.theme.defaultWhite};
    ${props => 
    props.black && 
    css`
        color: ${props => props.theme.text};
    `}

    h1 {
        font-size: 30px;
    }

    p {
        font-size: 1rem;
    }

    @media screen and (min-width: 360px) {
        h1 {
            font-size: 36px;
        }
        p {
            font-size: 1.1rem;
        }
    }
    @media screen and (min-width: 568px) {
        h1 {
            
        }
    }

    @media screen and (min-width: 768px) {
        h1 {
            font-size: 44px;
        }
        p {
            font-size: 1.2  rem;
        }
    }
    @media screen and (min-width: 1244px) {
        h1 {
            font-size: 72px;
        }
    }
`

export const BlackContainer = styled(motion.div)`
    
    padding-bottom: 30px;
    background-color: black;
    color: ${props => props.theme.defaultWhite};
    /* @media (min-width: 1024px) {
        max-width: 960px;
    }
    @media (min-width: 1216px) {
        max-width: 1152px;
    }
    @media (min-width: 1408px) {
        max-width: 1244px;
    } */

    /* ${props => props.fluid && css`
        padding:0;
        margin:0;
        max-width: 100%;
    `} */
`

export const PaddingTopBottom = styled.div`
    padding: 10vh 0;
    
`

export const Flex = styled.div`
    position: relative;
    display: flex;
    align-items: center;

    ${props => 
    props.spaceBetween && 
    css`
        justify-content: space-between; 
    `}

    ${props => 
    props.flexEnd && 
    css`
        justify-content: flex-end; 
    `}

    ${props => 
    props.alignTop && 
    css`
        justify-content: flex-start; 
    `}
    
    ${props => 
    props.noHeight && 
    css`
        height: 0;
    `}
`

export const Button = styled.div`
    button {
        padding: 1em 2em;
        border: 1px solid black;
        background-color: white;
        transition: 0.3s ease-in;

        &:hover {
            cursor: pointer;
            background-color: black;
            color: white;

        }

        a {
            color: white;
            text-decoration: none;
        }
}
`